import './devbx.form.layout.section.css';

BX.addCustomEvent("DevBxWebFormCreated", function(app) {
    app.component('devbx-form-layout-section', {
        props: ['page', 'item', 'row', 'active', 'config'],
        mounted() {
            this.item.component = this;
        },
        unmounted() {
            this.item.component = false;
        },
        computed: {
            labelFormatted() {
                return this.$root.htmlFormatFields(BX.util.htmlspecialchars(this.config.label));
            },

            helpTextFormatted() {
                return this.$root.htmlFormatFields(this.config.helpText);
            },

        },
        template: `
    <div>
        <h2 class="devbx-webform-col-heading"  v-if="!config.labelHidden" v-html="labelFormatted"></h2>
        <div class="devbx-webform-col-content" v-if="config.helpText.length>0" v-html="helpTextFormatted"></div>
        <devbx-webform-form-row v-for="(row, index) in config.layout.rows" :key="index" v-bind:row="row"/>
    </div>
    `
    });
});